    body {
    	margin: 0;
    	padding: 0;
    	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    		sans-serif;
    	-webkit-font-smoothing: antialiased;
    	-moz-osx-font-smoothing: grayscale;
    }

    code {
    	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    		monospace;
    }

    .App {
    	display: flex;
    	justify-content: center;
    	align-items: center
    }

    .page {
    	background-color: whitesmoke;
    	width: 80%;
    	height: 500px;
    	display: flex;
    	justify-content: space-evenly;
    	align-items: center;
    	margin-top: 50px;
    	background-image: url("https://previews.123rf.com/images/prapann/prapann1802/prapann180200055/94966218-rough-white-paper-texture-background.jpg")
    }

    .baseballcard {
    	width: 300px;
    	height: 450px;
    	background-color: #fff;
    }

    .baseballcard.image-card {
    	width: 300px;
    	height: 450px;
    	background-image: url("https://imgc.allpostersimages.com/img/print/u-g-Q104W810.jpg?w=550&h=550&p=0");
    	margin-top: 10px;
    }

    .baseballcard .image-card {
    	display: flex;
    	justify-content: center;
    }

    .baseballcard .image-container {
    	flex-direction: column;
    	align-items: center;
    	width: 95%;
    	margin-top: 10px;
    	display: flex;
    	position: relative;
    	color: #fff;
    	font-size: 20px;
    }

    .team-font {
    	position: absolute;
    	top: 15px;
    	left: 25px;
    	font-size: 50px;
    }

    .mlb-container {
    	position: absolute;
    	left: initial;
    	right: 6px;
    	z-index: 100;
    }

    .mlb {
    	max-width: 30px;
    }

    .image-card .card-image {
    	margin: 0;
    	width: 100%;
    	height: auto;
    }

    .image-card .player-name-container {
    	display: flex;
    	justify-content: center;
    	align-items: center;
    	background-color: #0489CA;
    	position: absolute;
    	bottom: 0;
    	width: 100%;
    	height: 50px;
    	font-family: 'Libre Franklin', sans-serif;
    }

    .player-name {
    	position: relative;
    }

    .baseballcard .text-card {
    	margin: 0;
    	width: 100%;
    	height: 100%;
    }

    .baseballcard table {
    	margin: 0;
    	width: 100%;
    	height: 100%;
    	border: 1px solid black;
    }

    .baseballcard td, .baseballcard th {
    	border: 1px solid #ddd;
    	/* padding: 4  px; */
    	text-align: center;
    }

    tr:nth-child(even) {
    	background-color: #f2f2f2;
    }

    @media (max-width: 700px) and (min-width:600px) {
    	.image-card {
    		margin-top: 10%;
    	}

    	.player-name-container {
    		justify-content: initial !important;
    	}

    	.player-name {
    		padding-left: 10px;
    	}
    }

    @media (max-width: 599px) and (min-width: 499px) {
    	.image-card {
    		margin-top: 25%;
    	}

    	.player-name-container {
    		justify-content: initial !important;
    	}

    	.player-name {
    		padding-left: 10px;
    		font-size: 15px;
    	}
    }

    @media (max-width: 498px) {
    	.image-card {
    		margin-top: 50%;
    	}

    	.player-name-container {
    		justify-content: initial !important;
    	}

    	.player-name {
    		padding-left: 5px;
    		font-size: 15px;
    	}
    }